.react-calendar{
  padding:10px;
  width:350px
}

.react-calendar button{
  padding:10px
}

.react-calendar button:hover{
  background-color:#fff3
}

.react-calendar .react-calendar__navigation{
  padding:10px;
  display:flex
}

.react-calendar .react-calendar__tile--active{
  background-color:#f8c16c
}

.react-calendar .react-calendar__tile--active:hover{
  background-color:#fff3
}

.react-calendar .react-calendar__month-view__days__day--neighboringMonth{
  color:#999
}

.react-calendar .react-calendar__month-view__days__day--weekend{
  color:#c62a1f
}

.scroll-thumb{
  height:100%;
  overflow-y:auto
}

.scroll-thumb::-webkit-scrollbar{
  width:6px;
  background-color:#00000020
}

.scroll-thumb::-webkit-scrollbar-thumb{
  border-radius:10px;
  box-shadow:inset 0 0 6px #00000027;
  background-color:#eee
}

.scroll-thumb-x{
  width:100%;
  overflow-x:auto;
  overflow-y:hidden
}

.scroll-thumb-x::-webkit-scrollbar{
  height:6px;
  background-color:#00000020
}

.scroll-thumb-x::-webkit-scrollbar-thumb{
  border-radius:10px;
  box-shadow:inset 0 0 6px #00000027;
  background-color:#eee
}

